body {
	margin: 0;
	font-family: 'Concert One', cursive;
	font-size: 14px;
	text-rendering: optimizeLegibility;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
	--index: calc(1vw + 1vh);
}

.fadeOut {
    -webkit-opacity: 0;
    opacity: 0;
    -webkit-transition: -webkit-opacity .75s ease-out;
    will-change: -webkit-opacity;
    transition: opacity .75s ease-out;
    will-change: opacity;
}

.fadeIn {
    -webkit-opacity: 1;
    opacity: 1;
    -webkit-transition: -webkit-opacity .75s ease-out;
    will-change: -webkit-opacity;
    transition: opacity .75s ease-out;
    will-change: opacity;
}

